@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  font-family: "Poppins, sans-serif";
}

header {
  position: relative;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  background: black;
  padding: 0 90px;
  height: 80px;
  width: 100%;
  z-index: 1000;
}
nav {
  position: absolute;
  text-align: center;
  top: 100%;
  left: 0;
  background: black;
  width: 100%;
  transform: scale(1, 0);
  transform-origin: top;
  transition: all 400ms ease-in-out;
}

.logo {
  color: #fff;
  font-size: 24px;
  text-decoration: none;
  letter-spacing: 1.5px;
  font-weight: 600;
  margin-right: 10px;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav ul li {
  margin-bottom: 1em;
  margin-left: 1em;
}

nav a.active,
nav a:hover {
  background-color: #093a83;
}

nav ul li a {
  padding: 5px 20px;
  color: #fff;
  display: flex;
  text-decoration: none;
  cursor: pointer;
  font-size: 1rem;
  text-transform: capitalize;
  opacity: 0;
  transition: opacity 200ms ease-in-out 200ms;
}
.navToggle:checked ~ nav {
  transform: scale(1, 1);
}

.navToggle {
  display: none;
}

.navLabel {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 2em;
  height: 100%;
  display: flex;
  align-items: center;
}

.navLabel span,
.navLabel span::before,
.navLabel span::after {
  display: block;
  background: white;
  height: 2px;
  width: 2em;
  border-radius: 2px;
  position: relative;
}

.navLabel span::before,
.navLabel span::after {
  content: "";
  position: absolute;
}

.navLabel span::before {
  bottom: 7px;
}

.navLabel span::after {
  top: 7px;
}

.navToggle:checked ~ nav a {
  opacity: 1;
  transition: opacity 200ms ease-in-out 200ms;
}

@media screen and (min-width: 800px) {
  .navLabel {
    display: none;
  }
  header {
    display: grid;
    grid-template-columns: 1fr auto minmax(600px 3fr) 1fr;
  }

  .logo {
    grid-column: 1 / 3;
    margin-left: 10px;
  }

  nav {
    all: unset;
    grid-column: 3 / 4;
    display: grid;
  }
  nav ul {
    display: flex;
    justify-content: flex-end;
  }

  nav ul li a {
    padding: 10px 15px;
    margin-top: 20px;
    margin-right: -10px;
    color: #fff;
    position: relative;
    text-decoration: none;

    font-size: 16px;

    opacity: 1;
    justify-content: flex-end;
  }

  nav a::before {
    content: "";
    display: block;
    height: 5px;
    background: #093a83;
    position: absolute;
    top: -0.5em;
    left: 0;
    right: 0;
    transform: scale(0, 1);
    transition: transform ease-in-out 250ms;
  }
  nav a:hover::before {
    transform: scale(1, 1);
  }
}
