@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

section {
  min-height: 100vh;
  padding: 100px;
}

.cont_sec {
  position: relative;
  background: #dde6f4;
  min-height: 100vh;
  width: 100%;
}

.cont_sec .title h3 {
  font-size: 36px;
  color: #093a83;
  text-decoration: solid underline #093a83 4px;
  font-style: italic;
  margin-bottom: 20px;
  margin-top: -50px;
  margin-left: -30px;
}

.container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.container .contactFormular1 {
  width: 50%;
  display: flex;
  flex-direction: column;
  position: relative;
  background: white;
  color: #333;
  min-height: 250px;
  padding: 50px;
}

.container .contactFormular1 .box {
  position: relative;
  padding: 15px 0;
  display: flex;
}

.contactFormular1 .box .boxIcon {
  min-width: 60px;
  height: 60px;
  background: #dde6f4;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 22px;
}

.contactFormular1 .box:hover .boxIcon {
  background: #55c4f3;
}

.container .contactFormular1 .box .boxContent h4:hover {
  color: #55c4f3;
}

.container .contactFormular1 .box .boxContent {
  display: flex;
  margin-left: 15px;
  flex-direction: column;
  font-size: 16px;
  font-weight: 400;
}

.container .contactFormular1 .box .boxContent h4 {
  display: flex;
  font-size: 18px;
  margin-bottom: 10px;
  color: #093a83;
}

@media screen and (max-width: 650px) {
  .cont_sec {
    padding: 20px;
    max-width: 100%;
  }

  .cont_sec .title h3 {
    font-size: 36px;
    color: #093a83;
    text-decoration: solid underline #093a83 4px;
    font-style: italic;
    margin-bottom: 20px;
    margin-top: 30px;
    margin-left: 20px;
  }

  .cont_sec .title {
    margin-left: 40px;
    margin-right: 40px;
    font-size: 18px;
  }
  .container {
    flex-direction: column;
    display: flex;
    max-width: 100%;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .container .contactFormular1 {
    display: flex;
    margin-bottom: 40px;
    max-width: 100%;
    background: white;
  }
  .container .contactFormular1 .box {
    position: relative;
    max-width: 100%;
    padding: 15px 0;
    display: flex;
  }

  .container .contactFormular1 .box .boxContent {
    display: flex;
    margin-left: 8px;
    max-width: 100%;
    flex-direction: column;
    font-weight: 400;
  }

  .container .contactFormular1 .box .boxContent h4 {
    display: flex;
    font-size: 15px;
    margin-bottom: 8px;
    color: #093a83;
  }
  .container .contactFormular1 .box .boxContent p {
    display: flex;
    font-size: 13px;
    margin-bottom: 8px;
    color: black;
  }
  .container .contactFormular1,
  .contactFormular2 {
    display: flex;
    width: 90%;
  }

  .container .contactFormular2 h2 {
    font-size: 24px;
  }

  .cont_sec .contactFormular2 .inputBox input,
  .cont_sec .contactFormular2 .inputBox textarea {
    width: 100%;
    padding: 5px 0;
    font-size: 1em;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    border-radius: 15px;
    padding-left: 10px;
  }
  .cont_sec .contactFormular2 .inputBox span {
    position: absolute;
    left: 0;
    pointer-events: none;
    padding: 10px 5px;
    margin: 10px 0;
    font-size: 1em;
    color: #777;
    transition: 0.5s;
    margin-left: 10px;
    padding-left: 10px;
  }
}

@media screen and (max-width: 991px) {
  .cont_sec {
    max-width: 100%;
    padding: 20px;
  }

  .cont_sec .title h3 {
    font-size: 36px;
    color: #093a83;
    text-decoration: solid underline #093a83 4px;
    font-style: italic;
    margin-bottom: 20px;
    margin-top: 30px;
  }

  .cont_sec .title {
    margin-left: 40px;
    margin-right: 40px;
  }
  .container {
    flex-direction: column;
    max-width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
  }

  .container .contactFormular1 {
    display: flex;
    max-width: 100%;
    flex-direction: column;
    margin-bottom: 40px;
    background: white;
  }

  .container .contactFormular1 .box {
    position: relative;
    max-width: 100%;
    padding: 15px 0;
    display: flex;
  }

  .cont_sec .contactFormular1,
  .contactFormular2 {
    display: flex;
    max-width: 100%;
  }

  .container .contactFormular2 .inputBox input,
  .container .contactFormular2 .inputBox textarea {
    max-width: 100%;
    padding: 5px 0;
    font-size: 0.9em;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    border-radius: 15px;
    padding-left: 10px;
  }
  .container .contactFormular2 .inputBox span {
    position: absolute;
    left: 0;
    pointer-events: none;
    padding: 10px 5px;
    margin: 10px 0;
    font-size: 1em;
    color: #777;
    transition: 0.5s;
    margin-left: 10px;
    padding-left: 10px;
  }
  .container .contactFormular2 {
    max-width: 100%;
    position: relative;
    background: #093a83;
    color: #dde6f4;
    min-height: 450px;
    padding: 60px;
  }
}

@media screen and (max-width: 1291px) {
  .cont_sec {
    padding: 20px;
    max-width: 100%;
  }
  .container .contactFormular1,
  .contactFormular2 {
    display: flex;
    width: 100%;
  }
  .cont_sec .title h3 {
    font-size: 36px;
    color: #093a83;
    text-decoration: solid underline #093a83 4px;
    font-style: italic;
    margin-bottom: 20px;
    margin-top: 30px;
  }
  .cont_sec .contactFormular2 .inputBox input,
  .cont_sec .contactFormular2 .inputBox textarea {
    width: 100%;
    padding: 5px 0;
    font-size: 1em;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
    border-radius: 15px;
    padding-left: 10px;
  }
  .cont_sec .contactFormular2 .inputBox span {
    position: absolute;
    left: 0;
    pointer-events: none;
    padding: 10px 5px;
    margin: 10px 0;
    font-size: 1.2em;
    color: #777;
    transition: 0.5s;
    margin-left: 10px;
    padding-left: 10px;
  }
  .cont_sec .contactInfos .box {
    position: relative;
    padding: 20px 0;
    display: flex;
    cursor: pointer;
  }
  .cont_sec .contactFormular2 .inputBox {
    position: relative;
    width: 100%;
    margin-top: 10px;
    padding-left: 10px;
  }
}

.cont_sec .contactFormular2 {
  width: 90%;
  position: relative;
  background: #093a83;
  color: #dde6f4;
  min-height: 450px;
  padding: 60px;
}

.cont_sec .contactFormular2 h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
  margin-top: -20px;
  color: white;
  font-weight: 500;
  display: flex;
  justify-content: center;
}

.cont_sec .contactFormular2 h6 {
  font-size: 0.8em;
  margin-left: -10px;
  display: flex;
  justify-content: center;
}

.cont_sec .contactFormular2 .inputBox {
  position: relative;
  width: 100%;
  margin-top: 10px;
  padding-left: 10px;
}

.cont_sec .contactFormular2 .inputBox input,
.cont_sec .contactFormular2 .inputBox textarea {
  width: 100%;
  padding: 5px 0;
  font-size: 1.1em;
  margin: 10px 0;
  border: none;
  border-bottom: 2px solid #333;
  outline: none;
  resize: none;
  border-radius: 15px;
  padding-left: 10px;
}
.cont_sec .contactFormular2 .inputBox span {
  position: absolute;
  left: 0;
  pointer-events: none;
  padding: 10px 5px;
  margin: 10px 0;
  font-size: 1.2em;
  color: #777;
  transition: 0.5s;
  margin-left: 10px;
  padding-left: 10px;
}

.cont_sec .contactFormular2 .inputBox input:focus ~ span,
.cont_sec .contactFormular2 .inputBox textarea:focus ~ span,
.cont_sec .contactFormular2 .inputBox input:valid ~ span,
.cont_sec .contactFormular2 .inputBox textarea:valid ~ span {
  color: #55c4f3;
  font-size: 0.9em;
  font-weight: bold;
  transform: translateY(-30px);
}

.cont_sec .contactInfos .box {
  position: relative;
  padding: 20px 0;
  display: flex;
  cursor: pointer;
}

.container .contactInfos .box .icon {
  min-width: 50px;
  height: 50px;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.75em;
}

.container .contactInfos .box:hover .icon {
  color: #fff;
  transition: 0.5s;
}

.container .contactInfos .box .text {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
  font-size: 0.9em;
  color: #fff;
  font-weight: 400;
}

.container .contactInfos .box .text h3 {
  font-weight: 500;
  color: white;
}

.container .contactInfos .text_2 {
  color: #fff;
  margin-top: 50px;
  font-weight: 500;
  padding-left: 10px;
  border-left: 50px solid var(--color-bg1);
  line-height: 1.5em;
}
