@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

section {
  min-height: 100vh;
  padding: 100px;
}

.about_sec {
  background: #dde6f4;
  min-height: 100vh;
  width: 100%;
}

.container {
  width: 1000px;
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
}

.about_sec .title h3 {
  font-size: 36px;
  color: #093a83;
  text-decoration: solid underline #093a83 4px;
  font-style: italic;
  margin-bottom: 20px;
  margin-top: -50px;
  margin-left: -10px;
}

.about_sec .title2 h3 {
  font-size: 32px;
  color: #55c4f3;
  text-decoration: solid underline #55c4f3 4px;
  margin-top: 25px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Soft Skills  */
svg {
  position: relative;
  width: 110px;
  height: 110px;
  z-index: 1000;
}
svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke: #191919;
  stroke-width: 10;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}

svg circle:nth-child(2) {
  stroke-dasharray: 0;
  stroke-dashoffset: 0;
}

.card:nth-child(1) svg circle:nth-child(2) {
  stroke: #093a83;
}
.card:nth-child(2) svg circle:nth-child(2) {
  stroke: #093a83;
}
.card:nth-child(3) svg circle:nth-child(2) {
  stroke: #093a83;
}
.card:nth-child(4) svg circle:nth-child(2) {
  stroke: #093a83;
}
.card:nth-child(5) svg circle:nth-child(2) {
  stroke: #093a83;
}
.card:nth-child(6) svg circle:nth-child(2) {
  stroke: #093a83;
}
.percent {
  position: relative;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  z-index: 1000;
}

.container .card {
  position: relative;
  width: 300px;
  background: #dde6f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  border-radius: 10px;
  text-align: center;
  overflow: hidden;
  transition: 0.5s;
  margin-right: 25px;
  margin-top: 15px;
}

.container .card:hover {
  background: black;
}

.container .card::before {
  content: "";
  position: absolute;
  bottom: 0%;
  width: 100%;
  height: 100%;
  background: rgba(62, 127, 224, 0.1);
  pointer-events: none;
  z-index: 1;
}

.percent .number {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.percent .number h3 {
  color: black;
  font-weight: 600;
  font-size: 28px;
  transition: 0.5s;
}
.card:hover .percent .number h3 {
  color: white;
  font-size: 36px;
}
.text {
  position: relative;
  color: #000;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1px;
  text-transform: capitalize;
  transition: 0.5s;
  margin-top: 20px;
}

.text2 {
  position: relative;
  color: #000;
  font-weight: 600;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: capitalize;
  transition: 0.5s;
  margin-top: 20px;
}

.card:hover .text,
.card:hover .text2 {
  color: #fff;
}

@media (max-width: 600px) {
  .container {
    width: 1000px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .container .card {
    position: relative;
    width: 300px;
    background: #dde6f4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    border-radius: 10px;
    text-align: center;
    overflow: hidden;
    transition: 0.5s;
    margin-right: 30px;
    margin-top: 15px;
    margin-left: -50px;
  }
  .about_sec .title2 h3 {
    font-size: 24px;
    color: #55c4f3;
    margin-top: 25px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (min-width: 750px) and (max-width: 1291px) {
  .container {
    width: 1000px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .container .card {
    position: relative;
    width: 600px;
    background: #dde6f4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    border-radius: 4px;
    text-align: center;
    overflow: hidden;
    transition: 0.5s;
    margin-right: auto;
    margin-top: 15px;
    margin-left: auto;
  }
}
