@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

section {
  min-height: 100vh;
  padding: 150px;
}

.tech {
  position: relative;
  min-height: 100vh;
  padding: 50px 80px;
  display: flex;
  align-items: center;
  background: #55c4f3;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: row;
}
.tech .title h3 {
  font-size: 36px;
  color: #000;
  text-decoration: solid underline #000 4px;
  font-style: italic;
  margin-bottom: 20px;
}

.tech .container {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 50px 20px;
  border-radius: 15px;
  background: #dde6f4;
  color: #fff;
  max-width: 400px;
  margin: 20px;
  text-align: center;
  gap: 20px;
  overflow: hidden;
  cursor: pointer;
}

.tech .container h3 {
  margin-bottom: 10px;
  font-weight: 500;
  color: white;
  background: #000;
  padding: 12px 20px;
}

.container .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  padding-right: 10px;
}

.container .skills h4 {
  min-width: 180px;
  text-align: right;
  padding-right: 20px;
  color: #000;
  font-size: 20px;
}

.container .skills .rating {
  position: relative;
  display: flex;
  margin: 10px 0;
  flex-direction: row-reverse;
}

.container .skills .rating input {
  position: relative;
  width: 20px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-appearance: none;
  appearance: none;
  overflow: hidden;
}

.container .skills .rating input::before {
  content: "\f005";
  position: absolute;
  font-family: fontAwesome;
  font-size: 32px;
  position: absolute;
  left: 4px;
  color: #030b0f;
  transition: 0.5s;
}

.container .skills .rating input:nth-child(2n + 1)::before {
  right: 4px;
  left: initial;
}

.container .skills .rating input:hover ~ input::before,
.container .skills .rating input:hover::before,
.container .skills .rating input:checked ~ input::before,
.container .skills .rating input:checked::before {
  color: #1e7cfb;
}
@media screen and (max-width: 600px) {
  .tech .container {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    border-radius: 15px;
    background: #dde6f4;
    color: #fff;
    max-width: 500px;
    margin: 5px;
    text-align: center;
    gap: 10px;
    overflow: hidden;
    cursor: pointer;
  }
  .container .skills h4 {
    max-width: 120px;
    text-align: right;
    padding-right: 10px;
    color: #000;
    font-size: 12px;
  }

  .tech .container h3 {
    margin-bottom: 10px;
    font-weight: 500;
    color: white;
    background: #000;
    padding: 12px 16px;
  }
  .tech .title h3 {
    font-size: 36px;
    color: #093a83;
    text-decoration: solid underline #093a83 4px;
    font-style: italic;
    margin-bottom: 20px;
    margin-left: 1em;
  }
}

@media screen and (min-width: 400px) and (max-width: 1200px) {
  .tech .container {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    border-radius: 15px;
    background: #dde6f4;
    color: #fff;
    max-width: 800px;
    margin: 5px;
    text-align: center;
    gap: 10px;
    overflow: hidden;
    cursor: pointer;
  }
  .container .skills h4 {
    min-width: 130px;
    text-align: right;
    padding-right: 10px;
    color: #000;
    font-size: 16px;
  }
}
