.percent {
  position: relative;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  z-index: 1000;
}

.container .card {
  position: relative;
  width: 300px;
  background: #dde6f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  border-radius: 10px;
  text-align: center;
  overflow: hidden;
  transition: 0.5s;
  margin-right: 25px;
  margin-top: 15px;
}

.container .card:hover {
  background: black;
}

.container .card::before {
  content: "";
  position: absolute;
  bottom: 0%;
  width: 100%;
  height: 100%;
  background: rgba(62, 127, 224, 0.1);
  pointer-events: none;
  z-index: 1;
}

.percent .number {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
